<template lang="pug">
.card.login-form
  .card-body
    h3.card-title.text-center Log in to Codepen
    .card-text
      p {{ state }}
      br
      p {{ user }}
      P(v-if="state.loading") Loading!
      div.alert.alert-danger.alert-dismissible.fade.show( role="alert", v-if="state.is_error") Incorrect username or password.
      form(id="login" @submit.prevent="LoginOrCheck" action="#" method="post")
        
        .form-group
          label(for='username') Email address
          input#username.form-control.form-control-sm(type='text', v-model="state.username")

        .form-group
          label(for='password') Password
          input#password.form-control.form-control-sm(type='text', v-model="state.password")

        button.btn.btn-primary.btn-block Sign in

</template>

<script>
import { computed, reactive } from 'vue';
import axios from 'axios';
import { useStore } from 'vuex';
import cookie from '../store/helper.js';
import { useRoute } from 'vue-router'
export default {
    name: 'Login',
    setup() {

      var router = useRoute();
      var store = useStore();
      var user = computed(() => store.user)

      var state = reactive({
        username: 'yoursammick',
        password: 'adsfabu',
        is_error: false,
        loading: false
      });

      function LoginOrCheck(e){
        e.preventDefault();
        state.loading = true;
          // console.log(localStorage.users || []);
        axios.post('/login', {username: state.username, password: state.password}).then(async function(res) {
          if( +res.status == 200 && res.data.is_loggedin ) {
            alert('Login Successfully!')
            res = res.data;
            if(!cookie.hasCookie('user')) {
              cookie.setCookie('user', res.user.username, { 'max-age': 60 * 60 * 24 * 10 });
            }
            cookie.setCookie('users', cookie.hasCookie('users') ? cookie.getCookie('users').split('|').push(res.user.username).join('|') : [res.user.username].join('|'), { 'max-age': 60 * 60 * 24 * 10 });
            await store.dispatch( 'SELECTUSER', res.user.username );
            await store.dispatch( 'SETNEWUSER', res );
            state.loading = false;
            router.push('/profile');
          } else {
            state.is_error = true;
          }
        })
      }
      return {
        LoginOrCheck,
        state,
        user
      }

    },
}
</script>

