<template lang="pug">
<profilecard v-if="user" :profile="user"/>
.profile-lists.m-3.p-4.border.border-mute.form(v-if="user?.edge_owner_to_timeline_media")
    .row
        .col-sm-3(v-for="post in user.edge_owner_to_timeline_media.edges")
            img.img-responsive.border.boder-primary(v-if="post.node.__typename == 'GraphImage'", :src="post.node.thumbnail_resources[1].src")
</template>

<script>
import { computed } from 'vue';
import { useStore } from 'vuex';
import profilecard from '../components/profilecard';

export default {
    name: 'Posts',
    components: { profilecard },
    setup() {
        var store = useStore();
        var user = computed( () => {
            return store.state.userdata.user;
        })
        return {
            user
        }
    },
}
</script>

<style lang="less" scoped>

</style>