<template lang="pug">
<Dialog :show="state.showDialog" v-if="state.editaccount">
    <account :account="state.editaccount" @add-account="editAccountUpdate(state.editaccount)"/>
</Dialog>

.container.border.border-mute.mb-4.p-3(v-if="state.account")
    <account :account="state.account" @add-account="addAccount"/>

.profile-lists.p-4.border.border-mute(v-if="store.state.userdata.accounts")
    .profile.row.border-bottom.pt-2.pb-2.mb-2
        .col-4 
            p Names
        //- .col-3
            p Dates
        .col-4
            p Counts
        .col-4 
            p Actions
    .profile.row.border-bottom.pt-2.pb-2.mb-2(v-for="(acc,i) in store.state.userdata.accounts", :key="i")
        .col-4 {{ acc.username }}
        //- .col-3 {{ acc.id }}
        .col-4 {{ acc.postfrom }}
        .col-4 
            span.btn.btn-sm.btn-secondary.mr-3.mb-2.d-inline-block(@click="editAccount(acc.id)") Edit
            span.btn.btn-sm.btn-danger.d-inline-block.mb-2(@click="removeAccount(acc.id)") Remove


pre {{ state.showDialog }}
</template>

<script>
import { computed, reactive } from 'vue';
import { useStore } from 'vuex';
import account from '../components/account.vue';
import Dialog from '../components/Dialog';
import axios from 'axios';
export default {
    name: 'Accounts',
    components: { account, Dialog },
    setup() {

        var store = useStore();
        var accountdata = {
            id: 0,
            username: '',
            postfrom: 0,
            post: true,
            story: true,
            permissions: {
                post: false,
                story: false,
                watermark: false,
                captions_credit: false,
                clickable_caption: false,
            },
        }
        var state = reactive({
            account: accountdata,
            editaccount: false,
            showDialog: false
        })

        var accounts = computed(() => {
            return store.state.userdata.accounts.filter(el => Object.keys(el).length);
        })

        function addAccount(newaccount) {
            console.log('newaccount', newaccount);
            axios.post('/doaccout', { account: newaccount, username: store.state.user }).then(res => {
                store.dispatch('UpdateNewAccount', res.data.user);
                state.account = accountdata;
            })
        }  

        function editAccount(id) {
            state.editaccount = store.state.userdata.accounts.find(el => +el.id == +id);
            state.showDialog = true;
            console.log('id', id, state.editaccount);
        }

        function editAccountUpdate() {
            state.showDialog = false;
            axios.post('/doaccout', { account: state.editaccount, username: store.state.user }).then(res => {
                store.dispatch('UpdateEditAccount', res.data.user);
                state.editaccount = {};
            })
        }

        function removeAccount(id){
            store.dispatch('removeAccount', id)
        }

        return {
            store,
            accounts,
            state,
            addAccount,
            removeAccount,
            editAccount,
            editAccountUpdate
        }
        
    },
}
</script>

<style lang="less" scoped>

</style>