<template lang="pug">

.container.border.border-mute.mb-4.p-3.addNewPost

    .loading(v-if="state.is_adding")
        span.fas.fa-spinner.fa-4x.fa-spin

    p.text-primary(v-if="validatePost") <strong>Shortcode:</strong> "{{ validatePost }}"
    form(@submit.prevent="addNewPost()")
        input.d-block.w-100.mb-3(type="text", v-model="state.addNewPost", placeholder='https://www.instagram.com/p/"CM7leKEh89y"/?utm_source=ig_web_button_share_sheet')
        button(type="submit") Add New Post


.container.border.border-mute.mb-4.p-3
    .row.post-items
        .col-md-3.col-sm-3.col-xl-2.p-3.border.border-collapge.post-item(:class="{'uploadedall': (post.uploadedto.story && post.uploadedto.feed)}", v-if="posts" v-for="post,index in posts" :key="post.id")
            .content.d-block.text-center

                img.responsive.w-100.border.border-mute.h-auto(:src=" './saved/' + post.nlpath.thumbnail || post.nlpath.full", v-if="post.type == 'photo'")

                div.d-flex.pt-2.pb-2.justify-content-around.border-bottom.post-buttons


                    label(:for="post.id + 'story'")
                        .action-button-wrap(:class="{'text-success': post.uploadto.story, 'action-done': post.uploadedto.story }")
                            i.fas.fa-circle.action-button
                    input.d-none(:id="post.id + 'story'", v-model="post.uploadto.story", @change="dopost()", type="checkbox")

                    label(:for="post.id + 'feed'")
                        .action-button-wrap(:class="{'text-primary': post.uploadto.feed, 'action-done': post.uploadedto.feed}")
                            i.fas.fa-th.action-button
                    input.d-none(:id="post.id + 'feed'", v-model="post.uploadto.feed", @change="dopost()", type="checkbox")

                    .action-button-wrap
                        i.fas.fa-times-circle.text-danger.action-button(@click="removePost(index)")
                
                div.post-detail.mt-2.mb-2
                    a.d-inline-block.text-black(:href="'https://www.instagram.com/' + post?.owner?.username", target="_blank")
                        <i class="fas fa-user"></i> {{ post?.owner?.username || post?.owner || 'Nill' }}  
                    .d-inline-block(v-if="post.getfrom")
                        <i class="fas fa-sign-out-alt"></i> {{ post.getfrom }}
                
                .post-caption
                    textarea.w-100.action-button(name="postCaption", cols="10", rows="2") {{ replace_caption( post ) }}
                
        .col-12.col-md-12(v-else)
            p there is no posts to show up
</template>

<script>
import { computed, onMounted, reactive } from 'vue';
import { useStore } from 'vuex';
import axios from 'axios';

export default {
    name: 'Posts',
    setup() {

        var store = useStore();
        var state = reactive({
            addNewPost: '',
            is_adding: false
        });
        var posts = [];

        posts = computed(() => {
            return store.state.userdata.posts;
        });

        var validatePost = computed(() => {
            var regg = /^(?:https?:\/\/)?(?:www\.)?(?:instagram\.com(?:\/\w+)?\/p\/)([\w-]+)(?:\/)?(\?.*)?$/gm
            return regg.exec(state.addNewPost)?.[1];
        })

        function removePost(index){
            console.log('removepost', store.state.userdata.posts[index]);
            axios.post('/removepost', { post: store.state.userdata.posts[index], username: store.state.user })
            .then(res => {
                res = res.data;
                console.log(res);
                if( res.is_removed ) {
                    store.state.userdata.posts.splice(index, 1);
                    store.dispatch('dopost');
                }
            })
        }

        function dopost(){
            store.dispatch('dopost');
        }

        function addNewPost(){
            state.is_adding = true;
            axios.post('/addpost', { shortcode: validatePost.value, username: store.state.user })
            .then(res => {
                res = res.data;
                console.log(res);
                if( res.addpost ) {
                    store.state.userdata.posts.splice(0, 0, res.addpost);
                    state.addNewPost = '';
                    state.is_adding  = false;
                    store.dispatch('dopost');
                }
            })
        }

        function replace_caption(post) {
            var regu = new RegExp( post.owner.username, "g" )
            return post.caption.replace(regu, store.state.user);
        }

        onMounted(() => store.dispatch('dopost'));

        return {
            state,
            store,
            posts,
            dopost,
            removePost,
            addNewPost,
            validatePost,
            replace_caption
        }
    },
}
</script>

<style lang="less" scoped>
.post-items {
    .post-item {
        &.uploadedall {
            opacity: 0.5;
        }
        .post-buttons {
            border: 1px solid #ccc;
            border-top: 0;
            label, .action-button-wrap {
                cursor: pointer;
            }
        }
        .action-done {
            border-bottom: 2px solid red;
        }
    }
}
.addNewPost {
    position: relative;
    .loading {
        transition: all .5s;
        background: rgba(0, 0, 0, 0.6);
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        max-width: 100%;
        width: 100%;
        margin-left: -1rem;
        display: flex;
        justify-content: center;
        align-content: center;
        .fa-spinner{
            height: max-content;
            margin-top: 5%;
            color: white;
        }
    }
}
</style>