<template lang="pug">
.profile-lists.m-3.p-4.border.border-mute.form
  .text-center.bg-white.container
    img.img-responsive.user-profile.mx-auto.d-block.img-fluid.rounded-circle(:src="profile.profile_pic_url")
    h2.text-uppercase.mt-2.mb-1 {{ profile.full_name }}
    h6.mb-4 @{{ profile.username }}
    p.caption.text-center.m-auto.profile-caption(style="max-width:60%") {{ profile.biography }}
    .dropdown-divider
    .row-info.text-center.pt-3.pb-0
      .info.d-inline-block.pr-4 {{ profile.edge_owner_to_timeline_media.count }} post(s) 
      .info.d-inline-block.pr-4 {{ profile.edge_follow.count }} follower(s)
      .info.d-inline-block {{ profile.edge_followed_by.count }} following(s)
</template>

<script>
export default {
    name: 'card',
    props: {
      profile: {
        type: Object,
        required: true
      }
    },
    setup() {}
}
</script>