<template lang="pug">
.add-form
    form(method="post", @submit.prevent="submit")

        .selected-profile.text-center.mb-3(v-if="state.searchText && state.isSeaching === false")
            img.border.border-rounded(:src="state.selectedaccount.profile_pic_url")
            h3 {{ state.selectedaccount.full_name }}
            p {{ state.selectedaccount.username }}

        pre {{ state.searchText + '  ' +  state.isSeaching }}
        .form-group.mb-2
            label(for="username") Search Instagram User By Username/Name
            .search-page.mb-2
                .input-group.flex-nowrap
                    span.input-group-text#addon-wrapping
                        i.fas.fa-spinner.fa-spin(v-if="state.isSeaching")
                        i(v-else="!state.isSeaching") @
                    input.form-control(
                        type="text",
                        placeholder="Username",
                        aria-label="Username",
                        aria-describedby="addon-wrapping",
                        v-model="state.searchText",
                        @keyup.prevent="searchUsers()"
                    )
                ul.search-results.p-0.m-0.list-group(v-if="state.showauto && state.users.length > 0")
                    li.search-result.list-group-item.p-2.bg-mute(v-for="user in state.users", :key="user.user.pk", @click.prevent="SelectUser(user.user)")
                        img.d-inline-block.profile-image.mr-2.border.rounded-circle.shadow-sm(:src="user.user.profile_pic_url", width="48", height="48", style="vertical-align: baseline;")
                        div.d-inline-block
                            p.fs-6.d-inline.p-0.m-0(style="font-weight: 600") {{ user.user.full_name }}
                            p.d-block.p-0.m-0.fs-6(style="font-size: 90%; color: rgb(72, 72, 72)")
                                | {{ user.user.username }} 
                                i.fas.fa-check-circle(v-if="user.user.is_verified")
                            p.d-block.p-0.m-0.fs-6(style="font-size: 70%; color: rgb(72, 72, 72)") {{ user.user.search_social_context }}
        
        span(v-if="state.selectedaccount?.username && state.showauto === false")

            .form-group.mb-2
                label(for="username") PostFrom
                input.form-control.control-small(type="number" v-model.number="state.selectedaccount.postfrom" id="postfrom" placeholder="324")

            .row
                .col-sm-6
                    input(type="checkbox" name="mpost" id="mpost" v-model="state.selectedaccount.post")
                    label.d-inline-block.form-check-label(for="mpost") Download Post
                .col-sm-6
                    input(type="checkbox" name="mstory" id="mstory" v-model="state.selectedaccount.story")
                    label.d-inline-block.form-check-label(for="mstory") Download Story

            h4 Permissions
            .row.form-group.mt-2(v-if="state.selectedaccount.permissions")
                .col-sm-2
                    input(type="checkbox" name="post" id="post" v-model="state.selectedaccount.permissions.post")
                    label.d-inline-block.form-check-label(for="post") Post
                .col-sm-2
                    input(type="checkbox" name="story" id="story" v-model="state.selectedaccount.permissions.story")
                    label.d-inline-block.form-check-label(for="story") Story
                .col-sm-2
                    input(type="checkbox" name="watermark" id="watermark" v-model="state.selectedaccount.permissions.watermark")
                    label.d-inline-block.form-check-label(for="watermark") Watermark
                .col-sm-2
                    input(type="checkbox" name="captions_credit" id="captions_credit" v-model="state.selectedaccount.permissions.captions_credit")
                    label.d-inline-block.form-check-label(for="captions_credit") Captions Credit
                .col-sm-2(v-if="state.selectedaccount.permissions.captions_credit")
                    input(type="checkbox" name="clickable_caption" id="clickable_caption" v-model="state.selectedaccount.permissions.clickable_caption")
                    label.d-inline-block.form-check-label(for="clickable_caption") Clickable Caption
            button(type="submit", class="btn btn-secondary") Add Profile

        //- pre {{ account }}
        //- pre {{ state.selectedaccount }}

</template>

<script>
import { reactive } from 'vue';
import axios from 'axios';
import { useStore } from 'vuex';
export default {
    name: 'account',
    props: {
        account: {
            type: Object,
            require: true
        }
    },
    setup(props, ctx) {

        var typingTimer;
        var doneTypingInterval = 1500;

        var state = reactive({
            selectedaccount: JSON.parse(JSON.stringify(props.account)),
            searchText: '',
            isSeaching: false,
            showauto: false,
            users: []
        });
        var store = useStore();
        
        function searchUsers(){
            clearTimeout(typingTimer);
            state.isSeaching = false;
            if (state.searchText.length) {
                typingTimer = setTimeout(doneTyping, doneTypingInterval);
            }
        }

        //user is "finished typing," do something
        function doneTyping () {
            state.isSeaching = true;
            axios.post('/doaccout', { username: store.state.userdata.username, action: 'search', query: state.searchText }).then( res => {
                console.log('search', res);
                console.log('searchdad', res.data);
                state.users = res.data.users;
                state.isSeaching = false;
                state.showauto = true;
            })
        }

        function submit() {
            ctx.emit('add-account', state.selectedaccount);
            state.searchText = '';
            state.selectedaccount = JSON.parse(JSON.stringify(props.account));
        }
        function checkusername(params) {
            console.log(params);
        }

        function SelectUser(user) {
            state.selectedaccount = { ...state.selectedaccount, ...user };
            state.searchText = user.username;
            state.showauto = false;
            console.log('SelectUser', user);
        }

        return {
            state,
            submit,
            checkusername,
            searchUsers,
            SelectUser
        }
    }
}
</script>

<style lang="less" scoped>
.search-results {
    display: block;
    max-width: 100%;
    max-height: 300px;
    overflow: auto;
    li {
        &:hover {
            background: #d5d5d538;
            cursor: pointer;
            transition: .5s all;
        }
        &:first-child {
            border-top-left-radius: unset !important;
            border-top-right-radius: unset !important;
        }
    }
}
</style>